.AboutMe {
    display: flex;
    flex-direction: row;
    background-color: rgb(18, 20, 31);
    padding-left: 300px;
    margin-top: 10%;
    min-height: 100vh;
    @media screen and (max-width: 600px) {
        padding-left: 0;
    }
}

#aboutme-intro {
    margin-left: 5%;
    margin-right: 5%;
    margin-bottom: 0;
    color:rgb(164, 172, 196);
}


#aboutme {
    color: rgb(164, 172, 196);
    margin-left: 5%;
    margin-right: 5%;
    text-align: center;
}

#container-img {
    height: auto;
    width: auto;
    overflow: hidden;
}

#container-img img {
    max-width: 100%;
    width: auto;
    height: auto;
    padding-top: 15%;
    overflow: hidden;
}

#name {
    text-align: center;
    font-size: calc(30px + 4vw);
    color:rgb(255, 255, 255);
    margin-top: 0%;
    margin-left: 5%;
    margin-right: 5%;
    text-shadow: 2px 2px 5px rgb(20, 20, 20);;
}


