@import '../css/variables.scss';
@import '../settings.scss';

html {
    scroll-behavior: smooth;
}

.section-container {
    display: block;
    margin-top: 3rem;
    height: auto;
    width: 100%;
    position: relative;
    box-sizing: content-box;

    ul.in-section {
        width: 100%;
        margin-block-start: 0;
        margin-block-end: 0;
        margin-inline-start: 0;
        margin-inline-end: 0;
        padding-inline-start: 0;
        height: auto;
        box-sizing: content-box;
        text-align: center;
        display: flex;
        flex-wrap: wrap;
        flex-direction: row;
        justify-content: center;
        list-style: none;
    
        &.technological {
    
            img {
                -webkit-transition: all 0.5s ease-out;
                -moz-transition: all 0.5s ease;
                -ms-transition: all 0.5s ease;
                &:hover {
                    -webkit-transform: rotate(-90deg);
                    -moz-transform: rotate(-90deg);
                    -ms-transform: rotate(-90deg);
                    transform: rotate(-90deg);
                }
            }
    
            li {
                padding: 0 1rem 1rem 1rem;
                margin-right: 0.5rem;
                margin-left: 0.5rem;
                h5 {
                    margin: 0;
                    color: antiquewhite;
                }
            }
        }
    
        &.knowledge, &.strengths, &.experiences, &.educations {
            display:block;
            line-height: 60px;
    
            li {
                padding: 0 1rem 1rem 1rem;
                margin-right: 0.5rem;
                margin-left: 0.5rem;
                vertical-align: baseline;
    
                h5 {
                    text-align: left;
                    font-size:larger;
                    line-height: 30px;
                    margin: 0;
                    color: antiquewhite;
                }
    
                h6 {
                    padding-left: 10px;
                    text-align: left;
                    font-size: 16px;
                    line-height: 16px;
                    margin: 0;
                    color: rgb(170, 159, 144);
                }
                
                .container-logo {
                    padding-left: .5rem;
                    float: left;
                    width: 56px;
                    height: 56px;
                    display: inline-block;
                }
                
                .container-edu{
                    float: left;
                    position: absolute;
                    margin-left: 8.25rem;
                    display: block;
                }
    
                .intra-li {
                    margin-bottom: 5rem;
                    float: left;
                    width: 100%!important;
                    display: flex!important;
                    flex-direction: column!important;
                    align-items: flex-start;
                }
            }
            p {
                text-align: left;
                margin-inline-start: 0px;
                margin-inline-end: 0px;
                font-family: Nunito,sans-serif;
                color: #a4acc4;
                font-weight: 400;
                font-size: 1rem;
    
                &#duration {
                    margin-left: 1rem;
                    padding: 0;
                }
            }
    
        }
    }
    
}

section {
    box-sizing: content-box;
    display: block;
    height: auto;
    width: 100%;
    margin-top: 30%;
    &.is-last{
        margin-bottom: 80%;
    }
    &.is-first{
        margin-top: 80px;
        height: 80vh;
    }
}

.nav-container {

    &-main {
        text-align: center;
        position: fixed;
        z-index: 8;
        bottom: 0;
        right: 210px;
        left: 510px;
        ul {
            margin-block-start: 0em;
            margin-block-end: 0em;
            padding-inline-start: 0px;
        }
    }

    &-skills {
        z-index: 7;
        opacity:unset;
        text-align: center;
    }

    &-main, &-skills {
        @media screen and (max-width: 1024px){ // Mobile L
            right: 110px;
            left: 410px;
        }
    
        @media screen and (max-width: 600px){ // Mobile L
            right: 0;
            left: 0;
        }
    
        @media screen and (max-width: 425px){ // Mobile L
            right: 0;
            left: 0;
        }
    }
    
}



.nav-profile {
    text-align: center;
    border-radius: 6px;
    display: block;
    margin-bottom: 1rem;

    a {
        user-select: none;
    }

    &.main {

        background-color: darken($bg-container-page,3);
        a {
            text-transform: capitalize;
            margin-left: 2rem;
            margin-right: 2rem;
            z-index: 8;
            display: inline-block;
            font-weight: 500;
            text-decoration: none;
            color: aliceblue;
            padding: 20px 0;
            position: relative;
            &::after {
                position: absolute;
                content: "";
                left: 0;
                width: 0;
                height: 3px;
                background: #25d366;
                transition: .3s;
                bottom: 0;
            }
            &.is-current {
                color: #25d366;
                &::after {
                    width: 100%;
                }
            }
            @media screen and (max-width: 320px){ // Mobile L
                margin-left: 0.6rem;
                margin-right: 0.6rem;
            }
        }

    }

    &.skills {
        text-transform: capitalize;
        font-size: 12px;
        background: darken($linkedin,3);
        margin-left: 10%;
        margin-right: 10%;

        li {
            text-align: center;
            display: block;
            list-style: none;   
        }

        a { 
            padding: 20px 0px;
            text-transform: uppercase;
            margin: 1.2rem;
            font-size: 0.94rem;
            letter-spacing: 1px;
            display: block;
            color: rgb(164, 172, 196);
            font-weight: 600;
            position: relative;
            border-bottom-width: 4px;
            box-shadow: 0 0 0 0 1px rgba(#fff, .15) inset;
            transition: all 0.4s ease-out 0s;
            overflow: hidden;
            text-shadow: 0 -1px 1px rgba(#000, .5);
            text-decoration: none;
            box-sizing: border-box; 
            &:before {
                content: "";
                display: block;
                width: 0;
                left: 50%;
                position: absolute;
                background-color: rgb(164, 172, 196);
                bottom: 0;
                transition: all .7s;
                height: 3px;
            }
            
            &:hover::before{
            width: 100%;
            left: 0;
            }

            &.is-current {
                color: darken($linkedin, 20);
               &::before {
                background-color: darken($linkedin, 20);
                width: 100%;
                left: 0;
               }
            }

            @media screen and (max-width: 320px){ // Mobile L
                margin-left: 0.6rem;
                margin-right: 0.6rem;
            }
        }
    }

    ul {
        padding-inline-start: 0;
    }
}

.backto-top {
    right: 2rem;
    position: fixed;
    font-size: 2rem;
    z-index: 2000;
    display: flex;
    bottom: 1rem;
    width: 50px;
    height: 50px;
    line-height: 49px;
    border-radius: 50%;
    background-color: rgb(0, 0, 0);
    text-align: center;
    overflow: hidden;
    transition: all 0.5s;

    @media screen and (max-width: 425px){ // Mobile L
        bottom: 5rem;
    }

    @media screen and (max-width: 375px){ // Mobile L
        bottom: 1.5rem;
    }

    @media screen and (max-width: 320px){ // Mobile L
        bottom: 5rem;
    }

    svg {
        margin: 0.6999rem;
        font-size: 14px!important;
        color: rgb(255, 255, 255);
        width: 27px;
        height: 27px;
    }
}

.header-section {
    padding-top: 5%;
}

.loader-skills {
    background: transparent;
    text-align: center;
    width: 100%;
    padding-top: 10%;
}

.description-skill {
    width: 100%;
    text-align: center;
    border: 4px solid darken($title-color,20);
    font-style:initial;
    box-sizing: border-box;
    background-color: $title-color;
    color: $bg-container-page;
    line-height: 24px;
    margin-block-start: 0;
    margin-block-end: 0;
    margin-inline-start: 0;
    margin-inline-end: 0;
    margin: 0;
    margin-bottom:1.5rem;
    padding: 1rem;
}

.container-list {
    padding-top: 1.5rem;
    width: auto;
    height: auto;
}

.intra-li {
    margin-bottom: 5rem;
    float: left;
    justify-content: space-between!important;
    width: 100%!important;
    display: flex!important;
    flex-direction: column!important;
    vertical-align: baseline;
    align-items: flex-start;
}